"use client";

import { useState, useEffect } from "react";
import { useRouter } from "next/navigation";
import {
  Box,
  Button,
  Flex,
  Stack,
  Heading,
  Text,
  Input,
  IconButton,
  HStack,
  Link as ChakraLink,
  Separator,
  Field,
} from "@chakra-ui/react";
import { motion } from "framer-motion";
import { LuEye, LuEyeOff, LuMail, LuLock } from "react-icons/lu";
import { FaGoogle, FaGithub } from "react-icons/fa";
import { signIn } from "next-auth/react";
import { toaster } from "../components/ui/toaster";
import { ThemeToggle } from "../components/ui/theme-toggle"; // Your toggle component

const MotionBox = motion.create(Box);

export default function LoginPage() {
  const router = useRouter();
  const [showPassword, setShowPassword] = useState(false);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [rememberMe, setRememberMe] = useState(false);
  const [loading, setLoading] = useState(false);

  // Load remembered email on component mount
  useEffect(() => {
    const savedEmail = localStorage.getItem("rememberedEmail");
    if (savedEmail) {
      setEmail(savedEmail);
      setRememberMe(true);
    }
  }, []);

  const handleLogin = async (e: React.FormEvent) => {
    e.preventDefault();
    setLoading(true);

    // Handle "Remember Me" logic
    if (rememberMe) {
      localStorage.setItem("rememberedEmail", email);
    } else {
      localStorage.removeItem("rememberedEmail");
    }

    const result = await signIn("credentials", { email, password, redirect: false });
    
    if (result?.error) {
      toaster.create({ title: "Error", description: "Invalid credentials", type: "error" });
      setLoading(false);
    } else {
      router.push("/dashboard");
    }
  };

  return (
    <Flex minH="100vh" align="center" justify="center" bg="gray.50" _dark={{ bg: "gray.950" }} p={4} position="relative">
      
      {/* Theme Toggle Positioned Top Right */}
      <Box position="absolute" top="6" right="6">
        <ThemeToggle />
      </Box>

      <MotionBox
        initial={{ opacity: 0, scale: 0.95 }}
        animate={{ opacity: 1, scale: 1 }}
        transition={{ duration: 0.4 }}
        w="full"
        maxW="md"
      >
        <Box p={10} bg="white" _dark={{ bg: "gray.900" }} borderRadius="3xl" boxShadow="2xl" border="1px solid" borderColor="gray.100" _darkBorderColor="gray.800">
          <Stack gap={6}>
            <Stack align="center" textAlign="center">
              <Heading size="3xl" fontWeight="black">Welcome Back</Heading>
              <Text color="gray.500">Enter your details to access your account</Text>
            </Stack>

            <Stack gap={3}>
              <Button variant="outline" size="lg" width="100%" borderRadius="xl" gap={3}>
                <FaGoogle color="#EA4335" /> Google
              </Button>
              <Button variant="outline" size="lg" width="100%" borderRadius="xl" gap={3}>
                <FaGithub /> GitHub
              </Button>
            </Stack>

            <HStack width="100%">
              <Separator flex="1" />
              <Text fontSize="xs" color="gray.400" fontWeight="bold">OR</Text>
              <Separator flex="1" />
            </HStack>

            <form onSubmit={handleLogin}>
              <Stack gap={4}>
                <Field.Root>
                  <Field.Label fontWeight="bold" fontSize="xs">EMAIL ADDRESS</Field.Label>
                  <Box pos="relative" w="full">
                    <Box pos="absolute" left="4" top="50%" transform="translateY(-50%)" zIndex="1" color="gray.400"><LuMail /></Box>
                    <Input ps="12" size="lg" borderRadius="xl" variant="subtle" placeholder="name@company.com" value={email} onChange={(e) => setEmail(e.target.value)} required />
                  </Box>
                </Field.Root>

                <Field.Root>
                  <Field.Label fontWeight="bold" fontSize="xs">PASSWORD</Field.Label>
                  <Box pos="relative" w="full">
                    <Box pos="absolute" left="4" top="50%" transform="translateY(-50%)" zIndex="1" color="gray.400"><LuLock /></Box>
                    <Input ps="12" pe="12" size="lg" borderRadius="xl" variant="subtle" type={showPassword ? "text" : "password"} placeholder="••••••••" value={password} onChange={(e) => setPassword(e.target.value)} required />
                    <IconButton pos="absolute" right="2" top="50%" transform="translateY(-50%)" variant="ghost" onClick={() => setShowPassword(!showPassword)} aria-label="toggle password">
                      {showPassword ? <LuEyeOff /> : <LuEye />}
                    </IconButton>
                  </Box>
                </Field.Root>

                {/* Remember Me & Forgot Password Row */}
                <HStack justify="space-between" width="full">
                  <HStack gap="2" cursor="pointer" onClick={() => setRememberMe(!rememberMe)}>
                    <input 
                      type="checkbox" 
                      checked={rememberMe} 
                      onChange={(e) => setRememberMe(e.target.checked)}
                      style={{ width: '16px', height: '16px', accentColor: '#319795', cursor: 'pointer' }}
                    />
                    <Text fontSize="sm" fontWeight="medium" userSelect="none">Remember me</Text>
                  </HStack>
                  <ChakraLink fontSize="xs" color="teal.500" fontWeight="bold">Forgot Password?</ChakraLink>
                </HStack>

                <Button type="submit" colorPalette="teal" size="xl" borderRadius="xl" loading={loading}>
                  Login to Account
                </Button>
              </Stack>
            </form>

            <Text textAlign="center" fontSize="sm">
              Don't have an account?{" "}
              <ChakraLink color="teal.600" fontWeight="bold" onClick={() => router.push("/signup")}>Create one</ChakraLink>
            </Text>
          </Stack>
        </Box>
      </MotionBox>
    </Flex>
  );
}